.transactionHistory {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.25),
    -4px 0px 3px rgba(0, 0, 0, 0.25);
}

.head {
    width: 200px;
    height: 50px;
    background-color: #124578;
    color: #fff;
    text-transform: uppercase;
}

.tableItem {
    text-align: center;
    height: 50px;
}

.tableItem:nth-child(2n) {
    background-color: rgb(119, 111, 111);
}